import Installation from '../Installation';

/**
 * @todo:
 * Document options.
 */
class Sparse extends Installation {

  constructor(patternBase, options) {
    super(patternBase, options);
  }

  /**
   * Simply renders an arrangement of 3 tiles at large size.
   *
   * @todo: As more arrangements are required, make more sophisticated
   * with parameters. OR just use entirely new models for simplicity.
   */
  fill()  {

    // Save the current co-ordinate system before we screw with it
    this.context.save();

    if (this.orientation === 'horizontal') {
      // Horizontal
      // Rotate 90 degrees.
      this.context.rotate(90 * Math.PI / 180);
    }
    else {
      // Vertical.
      // No rotation, just translation to the centre of the canvas.
      this.context.translate(
        this.canvas.width / 2 - (this.tileWidth * 2.5 / 2),
        this.canvas.height + this.tileHeight / 3
      );
    }

    // Add shadow to each tile.
    this.context.shadowOffsetX = 2;
    this.context.shadowOffsetY = 2;
    this.context.shadowBlur = 6;
    this.context.shadowColor = '#000000';

    // First Tile
    let xPos = this.tileHeight / 4 * 3;
    let yPos = 0;
    let tile = this.staticTiles ?  this.staticTiles[0] : Math.floor(Math.random() * 16);
    this.renderTile(tile, yPos, -xPos, 'horizontal', true);;

    // Second Tile
    xPos = this.tileHeight;
    yPos = this.tileWidth + 20;
    tile = this.staticTiles ?  this.staticTiles[1] : Math.floor(Math.random() * 16);
    this.renderTile(tile, yPos, -xPos, 'horizontal', true);

    // Third Tile
    xPos = this.tileHeight / 3 * 2;
    yPos = yPos + this.tileWidth / 5 * 2;
    tile = this.staticTiles ?  this.staticTiles[2] : Math.floor(Math.random() * 16);
    this.renderTile(tile, yPos, -xPos, 'horizontal', true);

    // and restore the co-ords to how they were when we began
    this.context.restore();
  }
}

export default Sparse;