import Ashlar from './installations/Ashlar';
import Herringbone from './installations/Herringbone';
import Sparse from './installations/Sparse';
import getPatternBase from './PatternBase';

// @todo: Possibly remove these window functions at some point.
// The window functions are used by the public/example.js driver.
// The exported classes are used in real production applications.
window.InstallBuilder = function() {
  return {
    ashlar: (patternBase, onPatternLoaded, overlayImgPath, useLoader) => {
      return new Ashlar(patternBase, onPatternLoaded, overlayImgPath, useLoader);
    },
    herringbone: (patternBase, onPatternLoaded, overlayImgPath, useLoader) => {
      return new Herringbone(patternBase, onPatternLoaded, overlayImgPath, useLoader);
    },
    sparse: (patternBase, onPatternLoaded, useLoader) => {
      return new Sparse(patternBase, onPatternLoaded, useLoader);
    },
    getPatternBase: getPatternBase,
  }
}();

export default { getPatternBase, installations: { Ashlar, Herringbone, Sparse } }