import "core-js/stable";
import "regenerator-runtime/runtime";

export default async function (id, imagePath, imageScale=1) {

  return new Promise((resolve, reject) => {

    let patternBase = document.createElement('img');
    patternBase.setAttribute('crossOrigin', 'anonymous');
    patternBase.src = imagePath;

    // Create a Canvas to store the pattern base.
    let patternBaseCanvas = document.createElement('canvas');
    let patternBaseCanvasContext = patternBaseCanvas.getContext('2d');

    // Create a Canvas for the final installation render.
    let canvas = document.getElementById(id);
    let context = canvas.getContext('2d');

    // Disable anti-aliasing (Causes issues when scaling).
    context.webkitImageSmoothingEnabled = false;
    context.mozImageSmoothingEnabled = false;
    context.imageSmoothingEnabled = false;

    let resized = false;
    patternBase.onError = reject;
    patternBase.onload = (event) => {

      if (event.target.useLoader) {
        //let loader = document.getElementById('loader').style.display = 'none';
      }

      if (!resized) {
        patternBaseCanvas.width = patternBase.width *= imageScale;
        patternBaseCanvas.height = patternBase.height *= imageScale;
        patternBaseCanvasContext.drawImage(patternBase, 0, 0, patternBaseCanvas.width, patternBaseCanvas.height);

        // This will trigger another .onload() event. The newly loaded image is the one we want
        // to perform further operations with, so for now we return and set a flag.
        patternBase.src = patternBaseCanvas.toDataURL("image/gif");

        resized = true;
      }
      else {
        resolve({ patternBaseCanvas, patternBase, canvas, context });
      }
    }
  });
};