import Installation from '../Installation';

/**
 * @todo:
 * Document options.
 */
class Herringbone extends Installation {

  constructor(patternBase, options) {
    super(patternBase, options);
  }

  /**
   * Should be called after this.image.onload() only.
   */
  fill()  {

    // Deal with scenarios where the provided image tile isn't horizontal.
    if (this.tileWidth < this.tileHeight) {

      // Save the current co-ordinate system before we screw with it
      this.context.save();

      this.fillInitialDiagonal();

      this.fillTop();

      this.fillBottom();

      this.context.restore();
    }
  };

  fillInitialDiagonal() {

    // Rotate 90 degrees.
    this.context.rotate(90 * Math.PI / 180);

    // Initialise current "Tile" position variables.
    let pos = {
      x: -this.tileHeight,
      y: this.canvas.height - this.tileWidth
    };
    // STEP 1: Render a single. diagonal line of horizontal tiles from bottom left corner up.

    // Render the horizontal herringbone tiles.
    //let iter = 0;
    //while (iter < 15) {
    this.renderHorizontalRow(pos);
  }

  fillBottom() {

    // Rotate the canvas:
    this.context.rotate(90 * Math.PI / -180);

    // Set the inital the x and y pos.
    let pos = {
      x: this.tileHeight,
      y: this.canvas.height - this.tileWidth
    };

    let verticalOrientation = true;

    let row = 1;

    let finished = false;

    while (!finished) {

      verticalOrientation ? this.renderVerticalRow(pos) : this.renderHorizontalRow(pos);

      row++;

      // Switch the orientation.
      verticalOrientation = !verticalOrientation;

      if (verticalOrientation) {
        // @todo: Abstract to pattern.setOrientationVertical();
        // Rotate teh canvas to be able to render vertical tiles.
        this.context.rotate(90 * Math.PI / -180);

        // Move xPos and yPos to the start of a new row.
        pos.x = (this.tileHeight * row);
        pos.y = this.canvas.height - this.tileWidth;

        // If the first tile of the new row is out of bounds, then the pattern is finished.
        finished = this.tileOutOfBounds(pos.x, pos.y);
      }
      else {
        // @todo: Abstract to pattern.setOrientationHorizontal();
        // Rotate teh canvas to be able to render horizontal tiles.
        this.context.rotate(90 * Math.PI / 180);

        // Move xPos and yPos to the start of a new row.
        pos.x = -this.tileHeight - (this.tileHeight * row);
        pos.y = this.canvas.height - this.tileWidth;

        // If the first tile of the new row is out of bounds, then the pattern is finished.
        finished = this.tileOutOfBounds(pos.x, pos.y, true);
      }
    }
  }

  fillTop() {

    // Rotate the canvas:
    this.context.rotate(90 * Math.PI / -180);

    // Set the inital the x and y pos.
    let pos = {
      x: -this.tileHeight,
      y: this.canvas.height - this.tileWidth
    };

    let verticalOrientation = true;

    let row = 0;

    let finished = false;

    while (!finished) {

      verticalOrientation ? this.renderVerticalRow(pos) : this.renderHorizontalRow(pos);

      row++;

      // Switch the orientation.
      verticalOrientation = !verticalOrientation;

      if (verticalOrientation) {
        // @todo: Abstract to pattern.setOrientationVertical();
        // Rotate teh canvas to be able to render vertical tiles.
        this.context.rotate(90 * Math.PI / -180);

        // Move xPos and yPos to the start of a new row.
        pos.x = 0;
        pos.y = this.canvas.height - this.tileWidth - this.tileHeight - this.tileHeight * row;

        // If the first tile of the new row is out of bounds, then the pattern is finished.
        finished = this.tileOutOfBounds(pos.x, pos.y);
      }
      else {

        // @todo: Abstract to pattern.setOrientationHorizontal();
        // Rotate teh canvas to be able to render horizontal tiles.
        this.context.rotate(90 * Math.PI / 180);

        // Move xPos and yPos to the start of a new row.
        pos.x = -this.tileWidth;
        pos.y = this.canvas.height - this.tileWidth * 2 - this.tileHeight * row;

        // If the first tile of the new row is out of bounds, then the pattern is finished.
        finished = this.tileOutOfBounds(pos.x, pos.y, true);
      }

    }
  }

  renderHorizontalRow(pos) {
    //let count = 0;
    while (!this.tileOutOfBounds(pos.x, pos.y, true)) {
      this.renderTile(Math.floor(Math.random() * 16), pos.y, pos.x);

      pos.x -= this.tileWidth;
      pos.y -= this.tileWidth;
      //count++;
    }
    //console.log('count', count);
  }

  renderVerticalRow(pos) {
    //let count = 0;
    while (!this.tileOutOfBounds(pos.x, pos.y)) {
      this.renderTile(Math.floor(Math.random() * 16), pos.x, pos.y);

      pos.x += this.tileWidth;
      pos.y -= this.tileWidth;
      //count++;
    }
    //console.log('count', count);
  }
}

export default Herringbone;