import getPatternBase from './PatternBase';
import "core-js/stable";
import "regenerator-runtime/runtime";

class Installation {

  constructor(patternBase, { onPatternLoaded, overlays, tiles, orientation }) {
    return (async () => {

      this.staticTiles = tiles;

      // @todo: Make more concise.
      this.patternBaseCanvas = patternBase.patternBaseCanvas;
      this.patternBase = patternBase.patternBase;
      this.canvas = patternBase.canvas;
      this.context = patternBase.context;

      this.tileWidth = this.patternBase.width / 8;
      this.tileHeight = this.patternBase.height / 2;

      this.orientation = orientation || 'horizontal';

      // Should always clear the canvas for a new fill.
      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height);

      // @todo: fill() should be asyncronous so we can properly add the overlay after we KNOW
      // the fill is definitely finished.
      this.fill();

      if (overlays) {
        overlays.forEach((overlayImage, index) => {
          let overlay = document.createElement('img');
          overlay.setAttribute('crossOrigin', 'anonymous');
          overlay.src = overlayImage.path;
          overlay.onload = () => {
            if (overlayImage.type === 'light_source') {
              this.context.globalCompositeOperation = 'soft-light';
            }
            this.context.drawImage(overlay, overlayImage.xPos, overlayImage.yPos, overlay.width, overlay.height);

            // Reset the composition operation so that only the previous drawImage call uses 'overlay'
            this.context.globalCompositeOperation = 'source-over'; // (Default).

            console.log(index);
            console.log(overlays.length);
            // if (onPatternLoaded) {
            //   onPatternLoaded();
            // }
          }
        });
      }
      // If the client defined a pattern loaded event function, invoke it now.
      else if (onPatternLoaded) {
        onPatternLoaded();
      }



      return this;
    })();
  }

  // Polymorphic fill function. Implement on sub classes for specific fill techniques.
  fill() {}

  /**
   * Render a tile at a given position on the canvas.
   *
   * @param tileNumber
   *   The "tile number" to render should be between 0 and 7.
   *
   * @param x
   *   x axis position to render at on the TARGET canvas.
   *
   * @param y
   *   y axis position to render at on the TARGET canvas.
   *
   * @param orientation
   *   "horizontal" or "vertical" orientation. Assumes given tile is already vertical.
   */
  renderTile(tileNumber = 0, x = 0, y = 0, orientation="horizontal", border=false) {

    let row = null;
    let column = null;

    if (tileNumber < 8) {
      row =  0;
      column = tileNumber;
    }
    else {
      row =  1;
      column = tileNumber - 8;
    }

    let patternX = column * this.tileWidth;
    let patternY = row * this.tileHeight;

    // Debug positions of tiles (render in place).
    //let context = this.patternBaseCanvas.getContext('2d');
    //let x = patternX+i;
    //let y = patternY+j;

    let context = this.canvas.getContext('2d');

    context.drawImage(this.patternBase, patternX, patternY, this.tileWidth, this.tileHeight, x, y, this.tileWidth, this.tileHeight);

    // Add a 1px 40% opacity white border to each tile to give tactile feel.
    border && this.addTileBorder(x, y);
  }

  tileOutOfBounds(x, y, flipped=false) {

    // Debug.
    // if (!flipped) {
    //   console.log('tile width', this.tileWidth);
    //   console.log('tile height', this.tileHeight);
    //   console.log(`x: ${x}, y: ${y}, cWidth: ${this.canvas.width}, cHeight: ${this.canvas.height}`);
    // }

    return (flipped) ? (y < 0 - this.tileWidth || x + this.tileHeight <= -this.canvas.width)  : (x >= this.canvas.width);
  }

  /**
   * Add a 1px 40% opacity white border to each tile to give tactile feel.
   *
   * Useful for individual tile renders rather than tessellating layouts (e.g Sparse).
   *
   * @param x
   *   Tile x position.
   * @param y
   *   Tile y position.
   */
  addTileBorder(x, y) {
    let context = this.canvas.getContext('2d');
    context.globalCompositeOperation = 'source-atop';
    context.globalAlpha = 0.4;
    context.strokeStyle = '#ffffff';
    context.lineWidth = 1;
    context.strokeRect(x, y, this.tileWidth, this.tileHeight);
    context.globalAlpha = 1;
    context.globalCompositeOperation = 'source-over'; // (Default).
  }
}

export default Installation;