import Installation from '../Installation';

/**
 * @todo:
 * Document options.
 */
class Ashlar extends Installation {

  constructor(patternBase, options) {
    super(patternBase, options);
  }

  /**
   * Should be called after this.image.onload() only.
   */
  fill()  {
    // Test placing the 16 tiles in the exact same layout as provided in the base pattern file.
    // Testing.
    // for (let i = 0; i < 8; i++) {
    //   this.renderTile(i, i * this.tileWidth, 0);
    // }
    // for (let i = 8; i < 16; i++) {
    //   this.renderTile(i, (i - 8) * this.tileWidth, this.tileHeight);
    // }
    // this.renderTile(0, 10 * this.tileWidth, 20);
    // return;

    // Initialise current "Tile" position variables.
    let xPos = 0;
    let yPos = 0;

    // Create a variable to store whether the current row should be "offset" to make the
    // "brick" pattern of Ashler.
    let offsetNextRow = true;

    // Deal with scenarios where the provided image tile isn't horizontal.
    if (this.tileWidth < this.tileHeight) {

      // Save the current co-ordinate system before we screw with it
      this.context.save();

      // Rotate 90 degrees.
      this.context.rotate(90 * Math.PI / 180);

      while(yPos < this.canvas.height + this.canvas.width) {

        // Check to see if we're at the end of a row.
        if (xPos > this.canvas.width + this.tileHeight) {
          // If yes, then drop down a row (width) and reset the column (height).
          // Again "Width" corresponds to Y position and "Height" to X since we've rotated the canvas.
          // Confusing right?
          yPos += this.tileWidth;
          xPos = (offsetNextRow) ? -(this.tileHeight/2) : 0;
          offsetNextRow = !offsetNextRow;
        }
        // Draw the next image, but move with the height, which now behaves as the width after rotation.
        //this.context.drawImage(this.image, yPos, -xPos, this.image.width, this.image.height);

        // @todo: Make "Random", but ensuring each set of 16 tiles contains the full 16 tiles in random order.
        this.renderTile(Math.floor(Math.random() * 16), yPos, -xPos);

        xPos += this.tileHeight;
      }

      // and restore the co-ords to how they were when we began
      this.context.restore();
    }
  };
}

export default Ashlar;